@font-face {
  font-family: 'maple';
  src: url('../public/Maplestory\ Bold.ttf') format('truetype');
}

* {
  font-family: 'maple';
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


input {
  border: 0px;
  padding: 3px;
  border-radius: 3px;
  outline: none;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 0px 0px 1px;
}

input:focus{
  box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 2px;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: linear-gradient(to right, #05f7fb7f,#1bff1b7f, #535ad0);
}

.container {
  height: 35rem;
  width: 38rem;
  overflow:hidden;
  border-radius: 8px;
  box-shadow: rgba(85, 224, 255, 0.7) 0px 48px 100px 0px;
  background-color: #ffffff;
}
.right-box {
  display: inline-block;
  margin: 0 auto;
  width: 19rem;
  height: 27rem;
  align-content: center;
  text-align: center;
}
.left-box {
  
  width: 19rem;
  height: 35rem;
  float: left;
  background: linear-gradient(70deg, #5cf850  38%, rgba(0,0,0,0) 30%),linear-gradient(120deg, #69e6f4  65%, rgba(0,0,0,0) 30%), linear-gradient(40deg, rgb(66, 50, 241) 60%, #e3d8ff 90%);
}

.left-box > span {
  max-width: 30rem;
  font-size:2.2rem;
  display: inline-block;
  padding-top: 13rem;
  margin: 1rem;
  font-weight: bold;
  color: white;
}


.right-box > h1{
  text-align: center;
  margin: 0 auto;
  font-weight: 900;
  width: min-content;
  line-height: 100%;
}

h3{
  color: #4000c7;
  margin-bottom: 3px;
}

.input-field {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 0 auto;
  margin-top: 3rem;
  width:12rem;
}
img {
  margin-top: 20px;
  width: 100px;
  height: 80px
}
.input-field > span {
  padding: 0.2rem;
  font-size:smaller;
  font-weight: 600;
  color:rgb(72, 65, 122);
}

.userinfo-field > article {
  margin-top: 1rem;
  padding: 0.3rem;
}

.checkbox-container {
  font-size: 11px;
  margin: 6px;
}
.checkbox-container > input {
  vertical-align:text-top;
}
button {
  border: 0px;
  background-color: #0545f7; 
  color: white;
  width: 12rem;
  height: 40px;
  border-radius: 6px;
  cursor: pointer;
  outline: none;
  margin-top: 1rem;
  font-size: 1rem;
  letter-spacing:5px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.signupBtn {
  border: 0px;
  background-color: #5cf850; 
  color: white;
  width: 12rem;
  height: 40px;
  border-radius: 6px;
  cursor: pointer;
  outline: none;
  margin-top: 1rem;
  font-size: 1rem;
  letter-spacing:5px;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

button:hover {
  background-color: #f2fcfc;
  color: rgb(34, 31, 31);
}

#alert-message {
  font-size:smaller;
  padding: 10px;
  color: rgb(189, 16, 16);
}

.userinfo-field {
  font-size: small;
  margin-top: 1rem;
}